import React, { useState, useCallback, useEffect } from "react";
import { Row, Col, Form, Button, Container,CloseButton } from "react-bootstrap";
import styles from "./Congratulations.module.css";

import { useHistory, NavLink, useLocation } from "react-router-dom";


const Login = () => {
    const {search} = useLocation();
    const query = new URLSearchParams(search);
  

  const history = useHistory();
  

  const email = query.get("e");
  const password = query.get("p");
  const token = query.get("t");
  console.log('params data', query.get('e'))
  useEffect(()=>{
      if(!email || !password || !token){
          history.replace('/');
      }else{
         localStorage.setItem("studentToken",token);
      }
  },[])

  const onCloseHandler = ()=>{
      history.push("/user");
  }
  
  return (
    <div className={`${styles.loginBody} p-2 min-vh-100  `}>
      <div className={styles.bgImg}>
        <Container>
          <Row>
            <Col md={1}></Col>
            <Col md={10} className={styles.bgWhite}>
              <Row className="m-0">
                <div className="text-dark">
                  <div className="text-end">
                    <CloseButton onClick={onCloseHandler} />
                  </div>
                  <h3 className="text-center" style={{ fontWeight: "bold" }}>
                    Congrats! your account created Successfully!
                  </h3>
                  <h5 className="text-muted text-center">
                    For instant clarification and urgert requiremnts. Help us to
                    serve you better
                  </h5>
                  <div className="mx-auto " style={{ width: "60%" }}>
                    <div className="bg-warning mt-3 p-3 px-5 rounded">
                      <h5 className="mx-auto text-muted">
                        {" "}
                        User:{" "}
                        <span
                          className="text-dark"
                          style={{ fontWeight: "bold" }}
                        >
                          {email}
                        </span>
                      </h5>
                      <h5 className="mx-auto text-muted">
                        {" "}
                        Password:{" "}
                        <span
                          className="font-weight-bold text-dark"
                          style={{ fontWeight: "bold" }}
                        >
                          {password}
                        </span>
                      </h5>
                    </div>
                    <img
                      src={"/images/reviews.png"}
                      alt="Login"
                      className="img-fluid w-50 mx-auto"
                    />
                    <h5 className="text-dark text-center">
                      <strong>$20 Credited Successfully in your wallet</strong>
                    </h5>
                    <p className="text-muted text-center">
                      *$5 can be used for an order value more than $50
                    </p>
                  </div>
                </div>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default Login;
